"use client"

import * as React from "react"
import { useState, useRef } from "react"
import { cn } from "@/lib/utils"
import { Button } from "@/components/ui/button"
import { Card, CardContent } from "@/components/ui/card"

export function ImageUploader() {
  const [imageSrc, setImageSrc] = useState<string | null>(null)
  const [error, setError] = useState<string | null>(null)
  const [isLoading, setIsLoading] = useState(false)
  const fileInputRef = useRef<HTMLInputElement>(null)

  const validateAndSetImage = (file: File) => {
    setIsLoading(true)
    setError(null)

    // Check if file is an image
    if (!file.type.startsWith('image/')) {
      setError('Please select a valid image file.')
      setIsLoading(false)
      return
    }

    const reader = new FileReader()
    reader.onload = (e) => {
      const dataUrl = e.target?.result as string
      
      // Create an image object to check dimensions
      const img = new Image()
      img.onload = () => {
        if (img.width > 1000 || img.height > 1000) {
          setError(`Image exceeds maximum allowed size of 1000 pixels. Current size: ${img.width}x${img.height}`)
          setIsLoading(false)
        } else {
          setImageSrc(dataUrl)
          setError(null)
          setIsLoading(false)
        }
      }
      img.onerror = () => {
        setError('Failed to load image. Please try another file.')
        setIsLoading(false)
      }
      img.src = dataUrl
    }
    reader.onerror = () => {
      setError('Failed to read file. Please try again.')
      setIsLoading(false)
    }
    reader.readAsDataURL(file)
  }

  const handleFileChange = (event: React.ChangeEvent<HTMLInputElement>) => {
    const file = event.target.files?.[0]
    if (file) {
      validateAndSetImage(file)
    }
  }

  const handlePaste = (event: React.ClipboardEvent) => {
    const items = event.clipboardData?.items
    if (items) {
      for (let i = 0; i < items.length; i++) {
        const item = items[i]
        if (item.type.startsWith('image/')) {
          const file = item.getAsFile()
          if (file) {
            validateAndSetImage(file)
            break
          }
        }
      }
    }
  }

  const handleCopy = async () => {
    if (imageSrc) {
      try {
        await navigator.clipboard.writeText(imageSrc)
        // You could add a toast notification here
        alert('Image URL copied to clipboard!')
      } catch (err) {
        console.error('Failed to copy:', err)
        alert('Failed to copy image URL. Please try again.')
      }
    }
  }

  const handleSelectClick = () => {
    fileInputRef.current?.click()
  }

  const handleReset = () => {
    setImageSrc(null)
    setError(null)
    if (fileInputRef.current) {
      fileInputRef.current.value = ''
    }
  }

  return (
    <div className="w-full max-w-2xl mx-auto space-y-6">
      <div className="text-center space-y-2">
        <h2 className="text-2xl font-bold text-foreground">Upload or Paste Your Image</h2>
        <p className="text-muted-foreground">Maximum size: 1000x1000 pixels</p>
      </div>

      <Card>
        <CardContent className="p-6">
          <div className="space-y-4">
            {/* File Input (Hidden) */}
            <input
              ref={fileInputRef}
              type="file"
              accept="image/*"
              onChange={handleFileChange}
              className="hidden"
            />

            {/* Upload Area */}
            <div
              className={cn(
                "border-2 border-dashed rounded-lg p-8 text-center transition-colors",
                "hover:border-primary/50 focus-within:border-primary",
                "cursor-pointer",
                error ? "border-destructive/50" : "border-border"
              )}
              onPaste={handlePaste}
              tabIndex={0}
              onClick={handleSelectClick}
            >
              {isLoading ? (
                <div className="space-y-2">
                  <div className="animate-spin w-8 h-8 border-2 border-primary border-t-transparent rounded-full mx-auto"></div>
                  <p className="text-muted-foreground">Processing image...</p>
                </div>
              ) : (
                <div className="space-y-4">
                  <div className="space-y-2">
                    <p className="text-lg font-medium">Click to select an image</p>
                    <p className="text-sm text-muted-foreground">or paste an image here (Ctrl+V)</p>
                  </div>
                  <Button variant="outline" type="button">
                    Select Image
                  </Button>
                </div>
              )}
            </div>

            {/* Error Message */}
            {error && (
              <div className="p-4 bg-destructive/10 border border-destructive/20 rounded-lg">
                <p className="text-destructive font-medium">{error}</p>
              </div>
            )}

            {/* Image Preview */}
            {imageSrc && (
              <div className="space-y-4">
                <div className="border rounded-lg p-4 bg-muted/30">
                  <img
                    src={imageSrc}
                    alt="Uploaded preview"
                    className="max-w-full max-h-96 mx-auto rounded-md shadow-sm"
                    style={{ maxWidth: '1000px', maxHeight: '1000px' }}
                  />
                </div>
                
                <div className="flex gap-2 justify-center">
                  <Button onClick={handleCopy} variant="default">
                    Copy Image URL
                  </Button>
                  <Button onClick={handleReset} variant="outline">
                    Upload Another
                  </Button>
                </div>
              </div>
            )}
          </div>
        </CardContent>
      </Card>
    </div>
  )
}
